import React, { Component } from 'react';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_MEDIA_NAME, GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../Services/Utils/utils';

export default class Video extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: '100',
      imgWidth: '100',
      error: '',
      imgUrl: props.url
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { height, width } = props;

    if (height && height !== "" && width && width !== "") {
      this.setState({
        imgHeight: (parseInt(height) * 100) / parseInt(width),
        imgWidth: (parseInt(width) * 100) / parseInt(height)
      })
    } else {
      // const image = await loadImage(imgUrl);
      // this.setState({
      //     height: (image.height * 100) / image.width,
      //     width: (image.width * 100) / image.height
      // })
    }
  }
  onErrorImageUpdate = event => {
    event.target.src = baseUrl(`media/images/error/image.svg`)
  }

  render() {
    const { socialNetwork, asset } = this.props
    const { imgUrl } = this.state

    const imageStyle = {
      paddingBottom: '100%', width: '100%'
    };


    return <div className="position-relative rounded-bottom-sm bg-light overflow-hidden" style={imageStyle}>
      <img className="position-absolute object-center-cover" loading="lazy" src={asset.postFile} height={260} width={260} alt={GET_MEDIA_NAME(imgUrl)} onError={this.onErrorImageUpdate} />

      <div className="position-absolute end-0 top-0 p-2 assets_video_ico">
        <span className="svg-icon svg-icon-2 me-0">
          <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 30 30">
            <path d="M0,0H30V30H0Z" fill="none" />
            <path d="M-946.639-512.1a2.364,2.364,0,0,1-2.36-2.361V-526.14a2.363,2.363,0,0,1,2.36-2.36h16.44a2.363,2.363,0,0,1,2.36,2.36V-514.5a2.339,2.339,0,0,1-.656,1.687,2.341,2.341,0,0,1-1.663.714Zm27.04-.921-6.6-3.72v-7.12l6.6-3.719a.412.412,0,0,1,.4.013.413.413,0,0,1,.2.347v13.84a.4.4,0,0,1-.193.357.4.4,0,0,1-.2.057A.4.4,0,0,1-919.6-513.021Z" transform="translate(949 535.5)" fill="#fff" />
          </svg>
        </span>
      </div>
      <div className="position-absolute p-2 end-0 bottom-0">
        {
          socialNetwork && socialNetwork.length > 0 ? <img className="img-fluid" loading="lazy" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(socialNetwork[0].id)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(socialNetwork[0].id)} /> : null
        }
      </div>
    </div>
  }
}
