import React, { Component } from 'react';
import Image from './Image';
import Video from './Video';
import TextReview from './TextReview';

export default class Media extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: '100',
      imgWidth: '100',
      error: '',
      imgUrl: props.url
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { height, width } = props;

    if (height && height !== "" && width && width !== "") {
      this.setState({
        imgHeight: (parseInt(height) * 100) / parseInt(width),
        imgWidth: (parseInt(width) * 100) / parseInt(height)
      })
    } else {
      // const image = await loadImage(imgUrl);
      // this.setState({
      //     height: (image.height * 100) / image.width,
      //     width: (image.width * 100) / image.height
      // })
    }
  }
  onErrorImageUpdate = event => event.target.src = "https://cloud.taggbox.com/widget/themes/media/images/no-image.svg"

  render() {
    const { asset, socialNetwork } = this.props
    return <>
      {
        asset.type == 1 ? <TextReview asset={asset} socialNetwork={socialNetwork} /> :
          (asset.type == 2 || asset.type == 4) ? <Image height={asset.mediaHeight} width={asset.mediaWidth} url={asset.postFile} postFile={asset.postFile} alt={asset.name} asset={asset} socialNetwork={socialNetwork} />
            : (asset.type == 3 || asset.type == 5) ? <Video asset={asset} socialNetwork={socialNetwork} /> : null



      }

    </>
  }
}
